* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body {
  font-size: 11px;
  font-family: "Roboto Slab", serif;
}
.wrapper {
  display: flex;
  height: 96vh;
  margin: 1em;
transition-duration: 2000ms;
  /* border: 1px solid red; */
}

h3 {
  font-weight: 500;
  letter-spacing: 0.25rem;
  padding: 0;
  margin: 0;
}
h4 {
  padding: 1em 0 1em 0;
}
.image-background {
  width: 2rem;
  height: 2rem;
  background-color: rgb(170, 167, 167);
  border-radius: 100%;
  position: relative;
}
.toggle-btns {
  background-color: rgba(211, 186, 167, 255);
  margin-bottom: 1.1rem;
}
.firstContainer {
  max-width: 22%;
  min-width: 330px;
  max-height: 100%;
  min-height: 100%;
  background-color: rgba(243, 243, 243, 255);
  padding: 2.5rem 2rem 2.5rem 1rem;
  margin: auto;
  overflow-y: scroll;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}
/* .firstContainer {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.firstContainer::-webkit-scrollbar {
  display: none;
} */

.firstDiv {
  width: 60%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem 0 2rem 0;
}

.views-img,
.heart-img,
.share-img,
.right-arrow,
.close {
  width: 1.2rem;
  border-radius: 100%;
  position: absolute;
  top: 20%;
  left: 20%;
}
.close {
  width: 20px;
  height: 20px;
}
.close-btn {
  position: absolute;
  left: -15px;
  top: 7%;
}
.secondDiv ul {
  list-style-type: none;
  padding-left: 0;
  margin-left: 0;
  line-height: 1.5em;
}
.thirdDiv p {
  line-height: 1.1rem;
}
.fourthDiv button {
  padding: 0.5rem;
  margin: 0.2rem;
  border-radius: 5px;
  font-size: 12px;
  margin-top: 2rem;
}
.btn-one {
  background-color: rgba(181, 122, 80, 255);
  border: none;
  color: white;
}
.btn-two {
  color: rgba(181, 122, 80, 255);
  border-color: rgba(181, 122, 80, 255);
}
.secondContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-height: 100%;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  position: relative;
}
.secondContainer img {
  height: 60%;
}
.moreText{
  display: none;
}
.readMore, .readLess {
  /* margin: 1em 0; */
  padding: 1em 0;
  border:none ;
  color: rgb(241, 72, 72);
}
@media screen and (max-width: 825px) {
  .wrapper {
    display: flex;
    flex-direction: column;
    height: fit-content;
  }
  .firstContainer {
    min-width: 100%;
    border-radius: 8px;
  }
  .secondContainer {
    border-radius: 8px;
    min-height: 60vh;
  }
}
